export default {
	data() {
		return {
			 active: 'home'
			}
	},
	methods: {
		onChange:function(event) {
			if (event.detail == "search"){
				this.$data.active = 'search'
				
			}
			else if (event.detail == "friends"){
				this.$data.active = 'friends'
			}
			else if (event.detail == "setting"){
				this.$data.active = 'setting'
			} 
	}
},
}
